import { styled } from "@mui/material"
import { pxToRem } from "@utils/styles" // Assuming your pxToRem utility

export const SectionContainer = styled("section")({
  padding: `${pxToRem(80)} 0` // 5rem is roughly 80px
})

export const ContentGrid = styled("div")(({ theme }) => ({
  containerType: "inline-size",
  margin: "0 auto",
  padding: `0 ${pxToRem(24)}`, // 1.5rem is roughly 24px
  display: "grid",
  gridTemplateColumns: "1fr",
  gap: pxToRem(48), // 3rem is roughly 48px
  alignItems: "center",
  [theme.breakpoints.up("md")]: {
    gridTemplateColumns: "repeat(2, 1fr)"
  }
}))

export const ImageContainer = styled("div")({
  position: "relative",
  height: pxToRem(400),
  borderRadius: pxToRem(16), // 1rem is roughly 16px
  overflow: "hidden",
  boxShadow: "0 25px 50px -12px rgba(0, 0, 0, 0.25)"
})

export const StyledImage = styled("img")({
  position: "absolute",
  inset: 0,
  width: "100%",
  height: "100%",
  objectFit: "cover"
})

export const ContentWrapper = styled("div")(({ theme }) => ({
  display: "flex",
  flexDirection: "column",
  gap: pxToRem(24),
  alignItems: "flex-start",
  justifyContent: "flex-start",
  [theme.breakpoints.down("md")]: {
    "& h4": {
      textAlign: "center"
    }
  }
}))

export const HeaderWrapper = styled("div")(({ theme }) => ({
  display: "flex",
  alignItems: "center",
  gap: pxToRem(16), // 1rem is roughly 16px,
  [theme.breakpoints.down("md")]: {
    justifyContent: "center",
    width: "100%"
  }
}))

export const HeaderIcon = styled("div")(({ theme }) => ({
  display: "flex",
  alignItems: "center",
  color: theme.palette.primary.main
}))

export const ActivitiesGrid = styled("div")(({ theme }) => ({
  display: "grid",
  gridTemplateColumns: "1fr",
  gap: pxToRem(24), // 1.5rem is roughly 24px
  [theme.breakpoints.up("md")]: {
    gridTemplateColumns: "repeat(2, 1fr)"
  },
  [theme.breakpoints.down("md")]: {
    gridTemplateColumns: "1fr",
    width: "100%"
  }
}))

export const ActivityCard = styled("div")({
  display: "flex",
  alignItems: "center",
  gap: pxToRem(12), // 0.75rem is roughly 12px
  padding: pxToRem(16), // 1rem is roughly 16px
  backgroundColor: "rgb(255 237 213)",
  borderRadius: pxToRem(8) // 0.5rem is roughly 8px
})

export const IconWrapper = styled("div")({
  color: "rgb(234 88 12)"
})
