import { styled } from "@mui/material"
import { pxToRem } from "@utils/styles"

export const HeroContainer = styled("div")({
  position: "relative",
  height: pxToRem(500),
  backgroundImage:
    'url("https://images.unsplash.com/photo-1600880292203-757bb62b4baf?auto=format&fit=crop&q=80&w=2940")',
  backgroundSize: "cover",
  backgroundPosition: "center",
  display: "flex", // Add this to make it a flex container
  alignItems: "center", // Align items vertically in the center
  justifyContent: "center" // Align items horizontally in the center
})

export const HeroOverlay = styled("div")({
  position: "absolute",
  inset: 0,
  background:
    "linear-gradient(to right, rgba(194, 65, 12, 0.8), rgba(194, 65, 12, 0.6))"
})

export const ContentWrapper = styled("div")({
  containerType: "inline-size",
  margin: "0 auto",
  padding: `0 ${pxToRem(24)}`,
  height: "100%",
  display: "flex",
  flexDirection: "column", // Stack items vertically
  alignItems: "center", // Align items horizontally in the center
  justifyContent: "center", // Align items vertically in the center
  textAlign: "center" // Center text within the container
})
